.js-slider__handler_active {
  transform: scale(1.4);
  z-index: 100;
  cursor: grabbing !important;
}
.js-slider__handler_active::after {
  content: '';
  position: absolute;
  top: -100vh;
  left: -100vh;
  display: block;
  width: 200vw;
  height: 200vh;
  opacity: 0;
  z-index: 10;
}
.js-slider__handler_active .js-slider__tooltip {
  display: inline-block;
}
.js-slider__handler {
  position: absolute;
  left: calc(100% - 2px);
  top: -6px;
  display: flex;
  justify-content: center;
  width: 15px;
  height: 15px;
  box-sizing: border-box;
  border: 1px solid rgba(211, 211, 211, 0.75);
  border-radius: 50%;
  background: white;
  outline: none;
  cursor: grab;
  transition: transform 0.3s ease;
}
